import styled from "styled-components";
import Link from "next/link";

const Container = styled.div`
  align-items: center;
  display: flex;
  flex-direction: column;
  margin: 0 auto 100px;
  justify-content: center;
`;

// to keep the active line and tabs together
const TabWrap = styled.div`
  display: inline-block;
`;

const TabRow = styled.div`
  display: flex;
  flex-direction: row;
`;

const Button = styled.div`
  align-items: center;
  cursor: pointer;
  display: flex;
  height: 35px;
  justify-content: center;
  text-align: center;
  width: 125px;
`;

const ActiveLine = styled.div`
  height: 2px;
  width: 125px;
  background: ${props => props.theme.blue};
  transition: all 0.15s ease-in-out;
`;

const Tab = ({ text, color, index, ...props }) => {
  // console.log(tabId);
  return (
    <Button onClick={() => props.switchTab(index)} style={{ color: color }}>
      {text}
    </Button>
  );
};

const TabBar = ({ tabs, activeTab, ...props }) => (
  <Container>
    {/* <div style={{ display: "inline-block" }}> */}
    <TabRow>
      {tabs.map((t, i) => {
        return (
          <Tab
            {...t}
            index={i}
            switchTab={props.switchTab}
            active={activeTab === i}
            key={t.text}
          />
        );
      })}
    </TabRow>
    <ActiveLine
      style={{
        backgroundColor: tabs[activeTab].color,
        transform: `translateX(${activeTab * 125}px)`
      }}
    />
    {/* </div> */}
  </Container>
);

export default TabBar;
